import { Component, OnInit } from '@angular/core';
import { Model } from 'src/app/api/models/model';
import { RequestService } from 'src/app/api/request.service';
import { ModalController } from '@ionic/angular';
import { SearchPage } from '../search/search.page';
import { ModalForm } from 'src/app/models/modal-form';
import { ModalFormInterface } from 'src/app/interfaces/modal-form-interface';
import { DialogService } from 'src/app/api/util/dialog.service';
import { AuthUserService } from 'src/app/services/auth-user.service';
import {environment} from 'src/environments/environment';
import { Router } from '@angular/router';
import { PlatformLocation } from '@angular/common';
import { ChatService } from 'src/app/services/chat.service';




@Component({
  selector: 'app-payment-team',
  templateUrl: './payment-team.page.html',
  styleUrls: ['./payment-team.page.scss'],
})
export class PaymentTeamPage extends ModalForm implements OnInit , ModalFormInterface {
  
  
  public modelTeam : Model;
  public modelMercadoPago : Model;
  
  public team = {
    id : '',
    name : '',
    admins : []
  }

  public tabsEnables = {
    info : true,
    name : true,
    admins : false,
    payment : false
  }

  public segmentSelect = '1';
  public errorName = '';
  public errorAdmins = '';
  public mp : any;

  constructor(
    public reqest : RequestService,
    public viewCtrl: ModalController,
    public modalController: ModalController,
    public dialogService : DialogService,
    public authUser : AuthUserService,
    public router : Router,
    location: PlatformLocation,
    public chatService : ChatService


  ) {
    super(location,viewCtrl);
    this.modelMercadoPago = new Model('MercadoPagoService',reqest);
    this.modelTeam = new Model('Team',reqest);
    this.team.admins.push({
      text : `${authUser.user.completeName} (${authUser.user.email})`,
      id : authUser.user.id
    });

    

  

    //Mercadopago.setPublishableKey(environment.mp_public_key); 
   
   }


  initObject() {
  }


  segmentChanged($event){
    //this.segmentSelect = $event.detail.value;
    //this.changedSegment($event.detail.value , this.segmentSelect < $event.detail.value)
  }


  changedSegment( segment , isBack = false){
    console.log(segment);

    if(isBack){
      this.segmentSelect = segment;
      return;
    }

    switch (segment) {
      case '2':
        this.verifyNameSelect()
        return;
      
      case '3':
          this.verifyAdmins();
          return;

      default :
        this.segmentSelect = segment 
    }
    
  }

  preferenceApi(){
    console.log('preference api')
      this.modelMercadoPago.api_function('createPreference',{}).subscribe(
        response => {
          console.log(response);
          if(response['status'] == 'success'){
            var preference_id = response['data'];
            console.log('pref' , preference_id)

            this.mp.checkout({
              preference: {
                  id: preference_id
              },
              render: {
                    container: '.cho-container', // Indica el nombre de la clase donde se mostrará el botón de pago
                    label: 'Pagar', // Cambia el texto del botón de pago (opcional)
              }
            })
          }


        },
        error => {
          console.error(error)
        }  
      )
  }


  async verifyNameSelect(){

    if(this.team.name == ''){
      this.errorName = 'El campo nombre es Obligario.';
      this.tabsEnables.admins = false;
      return;
    }


    await this.dialogService.loadingProgres('verificando nombre');
    console.log('team' , this.team);
    this.modelTeam.api_function('exist',this.team).subscribe(
      response => {
        this.dialogService.dimissLoaging();
        if(response['data']){
          this.errorName = `El equipo ${this.team.name} ya se encuentra Registrado.`;
          this.tabsEnables.admins = false;
          return;
        }

        this.errorName = '';
        this.segmentSelect = '2';
        this.tabsEnables.admins = true;
        console.log(response)  
      },
      error => {
        console.log(error)
        this.dialogService.dimissLoaging();
      }
    )
  }

  verifyAdmins(){
    this.errorAdmins = '';
    if(this.team.admins.length > 0){
      this.segmentSelect = '3';
      this.tabsEnables.payment = true
      //this.preferenceApi();
    }else{
      this.errorAdmins = 'Debe añadir como minimo un Administrador';
      this.tabsEnables.payment = false;

    }
  }



  dismiss( team ) {
    this.viewCtrl.dismiss(
      {
        team : team,
      }
    );
  }

  close(){
    this.viewCtrl.dismiss();
  }

  async addAdmins(e){
    this.errorAdmins = '';
    e.preventDefault();
    console.log('Add admins');


    
    let adminsDefault = this.team.admins;

    const modal = await this.modalController.create({
      component: SearchPage ,
      componentProps: { 
        cancellable: false,
        model : 'User',
        function : 'searchUser',
        multiple : true,
        textSearch : 'Nombre o email',
        listSelect : adminsDefault
      }
    });

    modal.onDidDismiss().then(data=>{
      if(data.data && data.data.items){
        const list = data.data['items'];
      //console.log('users_select' , list);
        this.team.admins = list;
      }
    })

    return await modal.present();
  }

  async saveTeam(){

    await this.dialogService.loadingProgres('Registrando');

    this.modelTeam.api_create(this.team).subscribe(
      response => {
        this.dialogService.dimissLoaging();
        if(response['status'] == 'success'){
          this.dialogService.showToast('Equipo registrado',null,'success');
          let team = response['Team'];
          this.authUser.user.teams.push(team);
          this.authUser.saveUser();
          this.dismiss(team);
          //this.router.navigateByUrl('/team/profile/'  + team.id);
        }else{
          alert('no success')
        }
      }
    ,
    error => {
      if(this.modelTeam.errorsForm.any()){
        
      }

      console.error(error);
      this.dialogService.dimissLoaging();
    },
    ()=>{
      this.dialogService.dimissLoaging();
    }
    )

  }

}
